<template>
  <div class="forgetPassword">
    <nav-head title="忘记密码"/>
    <div class="padding-0-25">
      <input type="text" @input="handleInputNum" maxlength="11" v-model="phone" placeholder="请输入手机号">
      <div class="relative">
        <input type="text" v-model="captcha" maxlength="6" placeholder="请输入短信验证码">
        <captcha-button :phone="phone" class="btn-captcha"></captcha-button>
      </div>
      <input type="text" minlength="6" maxlength="18" v-model="password" placeholder="请输入密码">
      <mt-button size="large" v-bind:disabled="!phone || !captcha || !password" @click.native="saveEdit" class="btn-modify" v-bind:class="{'available': phone && password && captcha}">确认修改</mt-button>
    </div>
  </div>
</template>
<script>
import NavHead from '@/components/NavHead.vue'
import CaptchaButton from '@/components/CaptchaButton'
import { Button, Toast } from 'mint-ui'
import { mapActions } from 'vuex'
import { FORGET_PASSWORD } from '@/vuex/Mine'
import Util from '@/tool/util'

export default {
  name: 'forgetPassword',
  data () {
    return {
      phone: '',
      captcha: '',
      password: ''
    }
  },
  methods: {
    saveEdit () {
      if (this.phone && this.password && this.captcha) {
        if (!Util.isMobile(this.phone)) {
          Toast('请输入正确的手机号!')
          return
        }
        if (this.password.length < 6) {
          Toast('请输入6-18位密码!')
          return
        }
        this.setPassword({
          username: this.phone,
          password: this.password,
          verification_code: this.captcha
        })
      }
    },
    handleInputNum (e) {
      this.phone = e.target.value.replace(/[^\d]/g, '')
    },
    ...mapActions({
      setPassword: FORGET_PASSWORD
    })
  },
  components: {
    NavHead,
    CaptchaButton,
    [Button.name]: Button
  }
}
</script>
<style scoped lang="scss">
  div.forgetPassword {
    position: absolute;
    min-height: 100%;
    width: 100%;
    text-align: center;
    background-color: $background-light-color;
    input {
      border: none;
      outline: none;
      width: 100%;
      font-size: 16px;
      height: 56px;
      line-height: 56px;
      margin-bottom: 10px;
      background: none;
      border-radius: 0;
      color: $font-color;
      @if $IF365 { border-bottom: 1px solid $border-color; }
      @else { border-bottom: 1px solid #DDD; }
    }
    .btn-captcha {
      position: absolute;
      margin-top: 5px;
      right: 0;
      top: 0;
    }
    .btn-modify {
      border: none;
      background-color: #ccc;
      color: white;
      box-shadow: none;
      height: 48px;
      font-size: 17px;
      margin-top: 20px;
    }
    .btn-modify.available{
      background-color: $back-color;
      @if $IF365 { color: #131313; }
    }
  }
  .padding-0-25 {
    padding: 0 25px;
  }
  .relative {
    position: relative;
  }
</style>
